import {
    SIGNUP_SUCCESS,
    SIGNUP_FAIL,
    ACTIVATION_SUCCESS,
    ACTIVATION_FAIL,
    SET_AUTH_LOADING,
    REMOVE_AUTH_LOADING,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    USER_LOADED_SUCCESS,
    USER_LOADED_FAIL,
    AUTHENTICATED_SUCCESS,
    AUTHENTICATED_FAIL,
    REFRESH_SUCCESS,
    REFRESH_FAIL,
    LOGOUT,
    RESET_PASSWORD_SUCCESS,
    RESET_PASSWORD_FAIL,
    RESET_PASSWORD_CONFIRM_SUCCESS,
    RESET_PASSWORD_CONFIRM_FAIL
} from './types'
import {setAlert} from './alert'
import axios from 'axios'

export const check_authenticated = () => async dispatch => {
    if(localStorage.getItem('access')){
        const config = {
            headers: {
                'Accept':'application/json',
                'Content-Type':'application/json'
            }
        }

        const body = JSON.stringify({
            token: localStorage.getItem('access')
        })

        try {
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/jwt/verify/`, body, config)

            if(res.status === 200){
                dispatch({
                    type: AUTHENTICATED_SUCCESS
                })
            } else {
                dispatch({
                    type: AUTHENTICATED_FAIL
                })
            }
        } catch (error) {
            dispatch({
                type: AUTHENTICATED_FAIL
            })
        }
    } else {
        dispatch({
            type: AUTHENTICATED_FAIL
        })
    }
};

export const signup = (first_name, last_name, email, password, re_password) => async dispatch => {
    dispatch({
        type: SET_AUTH_LOADING
    })

    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    };

    const body = JSON.stringify({
        first_name,
        last_name,
        email,
        password,
        re_password
    });

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/users/`, body, config);

        if (res.status === 201) {
            dispatch({
                type: SIGNUP_SUCCESS,
                payload: res.data 
            });
            dispatch(setAlert('Te enviamos un correo, por favor activa tu ceunta. Revisa tu bandeja de entrada.', 'green'))
        } else {
            dispatch({
                type: SIGNUP_FAIL
            });
            dispatch(setAlert('Error al crear cuenta.', 'red'))
        }
        dispatch({
            type: REMOVE_AUTH_LOADING
        })
    } catch (err) {
        dispatch({
            type: SIGNUP_FAIL
        });
        dispatch({
            type: REMOVE_AUTH_LOADING
        })
        dispatch(setAlert('Error al conectar con el servidor, intenta de nuevo mas tarde.', 'red'))
    }
};

export const load_user = () => async dispatch => {
    if(localStorage.getItem('access')){
        const config = {
            headers: {
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        }

        try {
            const res = await axios.get(`${process.env.REACT_APP_API_URL}/auth/users/me/`, config)

            if (res.status === 200){
                dispatch({
                    type: USER_LOADED_SUCCESS,
                    payload: res.data
                })
            } else {
                dispatch({
                    type: USER_LOADED_FAIL
                })
            }
        } catch (error) {
            dispatch({
                type: USER_LOADED_FAIL
            })
        }
    } else {
        dispatch({
            type: USER_LOADED_FAIL
        })
    }
};

export const login = (email, password) => async dispatch => {
    dispatch({
        type: SET_AUTH_LOADING
    })

    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    };

    const body = JSON.stringify({
        email,
        password
    });

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/jwt/create/`, body, config);

        if(res.status === 200){
            dispatch({
                type: LOGIN_SUCCESS,
                payload: res.data
            })
            dispatch(load_user())
            dispatch({
                type: REMOVE_AUTH_LOADING
            })
            dispatch(setAlert('Inicio de sesion exitoso', 'green'))
        }else{
            dispatch({
                type: LOGIN_FAIL
            })

            dispatch({
                type: REMOVE_AUTH_LOADING
            })
            dispatch(setAlert('Error al iniciar sesion', 'red'))
        }
    } catch (error) {
        dispatch(setAlert('Error al conectar con el servidor, intente mas tarde.', 'red'))
    }
};

export const activate = (uid, token) => async dispatch => {
    dispatch({
        type: SET_AUTH_LOADING
    })

    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    };

    const body = JSON.stringify({
        uid,
        token
    });

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/users/activation/`, body, config);

        if (res.status === 204) {
            dispatch({
                type: ACTIVATION_SUCCESS
            });
            dispatch(setAlert('Cuenta activada correctamente.', 'green'))
        } else {
            dispatch({
                type: ACTIVATION_FAIL
            });
            dispatch(setAlert('Error al activar cuenta.', 'red'))
        }
        dispatch({
            type: REMOVE_AUTH_LOADING
        })
    } catch (error) {
        dispatch({
            type: ACTIVATION_FAIL
        });
        dispatch({
            type: REMOVE_AUTH_LOADING
        })
        dispatch(setAlert('Error al conectar con el servidor.', 'red'))
    }
};

export const refresh = () => async dispatch => {
    if (localStorage.getItem('refresh')) {
        const config = {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        };

        const body = JSON.stringify({
            refresh: localStorage.getItem('refresh')
        });

        try {
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/jwt/refresh/`, body, config);
            
            if (res.status === 200) {
                dispatch({
                    type: REFRESH_SUCCESS,
                    payload: res.data
                })
            } else {
                dispatch({
                    type: REFRESH_FAIL
                })
            }
        }catch(err){
            console.log(err.response.data)
            dispatch({
                type: REFRESH_FAIL
            })
        }
    } else {
        dispatch({
            type: REFRESH_FAIL
        });
    }
};

export const reset_password = (email) => async dispatch => {
    dispatch({
        type: SET_AUTH_LOADING
    })

    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }

    const body = JSON.stringify({email})

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/users/reset_password/`, body, config)

        if ( res.status === 204){
            dispatch({
                type: RESET_PASSWORD_SUCCESS
            })
            dispatch({
                type: REMOVE_AUTH_LOADING
            })
            dispatch(setAlert('El correo para restablecer la contrasea ya fue enviado, revise su bandeja de entrada', 'green'))
        } else {
            dispatch({
                type: RESET_PASSWORD_FAIL
            })
            dispatch({
                type: REMOVE_AUTH_LOADING
            })
            dispatch(setAlert('Error enviando correo para restablecer contrasea', 'red'))
        }
    } catch (error) {
        dispatch({
            type: RESET_PASSWORD_FAIL
        })
        dispatch({
            type: REMOVE_AUTH_LOADING
        })
        dispatch(setAlert('Error enviando correo para restablecer contrasea', 'red'))
    }
}

export const reset_password_confirm = (uid, token, new_password, re_new_password) => async dispatch => {
    dispatch({
        type: SET_AUTH_LOADING
    })

    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }

    const body = JSON.stringify({
        uid,
        token,
        new_password,
        re_new_password
    })

    if(new_password !== re_new_password){
        dispatch({
            type: RESET_PASSWORD_CONFIRM_FAIL
        })
        dispatch({
            type: REMOVE_AUTH_LOADING
        })
        dispatch(setAlert('Las contraseas no coinciden', 'red'))
    } else {
        try {
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/users/reset_password_confirm/`, body, config)

            if(res.status === 204){
                dispatch({
                    type: RESET_PASSWORD_CONFIRM_SUCCESS
                })
                dispatch({
                    type: REMOVE_AUTH_LOADING
                })
                dispatch(setAlert('Su contrasea fue restablecida correctamente', 'success'))
            } else {
                dispatch({
                    type: RESET_PASSWORD_CONFIRM_FAIL
                })
                dispatch({
                    type: REMOVE_AUTH_LOADING
                })
                dispatch(setAlert('Error al cambiar la contrasea', 'danger'))
            }
        } catch (error) {
            dispatch({
                type: RESET_PASSWORD_CONFIRM_FAIL
            })
            dispatch({
                type: REMOVE_AUTH_LOADING
            })
            dispatch(setAlert('Error al cambiar la contrasea', 'danger'))
        }
    }
}

export const logout = () => async dispatch => {
    dispatch({
        type: LOGOUT
    })
    dispatch(setAlert('Cerraste sesion', 'green'))
}
